import "./App.css";
import { NavBarPoke } from "./NavBarPoke";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.min.js";
import "bootstrap/dist/js/bootstrap.bundle.min.js";
import { useEffect, useState } from "react";
import axios from "axios";

import { BrowserRouter, Link, Route, Routes } from "react-router-dom";
import { Home } from "./Home";
import { PokeProfile } from "./PokeProfile";

function App() {
  const [pokeget, setPokeget] = useState("");
  const [pokelist, setPokelist] = useState([]);

  const getpokelist = async () => {
    try {
      const response = await axios.get("https://pokeapi.co/api/v2/pokemon/");
      console.log(response);
      setPokelist(response.data.results);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getpokelist();
  }, [pokeget]);

  return (
    <BrowserRouter>
      {/* <NavBarPoke /> */}
      <Routes>
        <Route exact path="/Pokedex/" element={<Home pokelist={pokelist} />} />
        <Route exact path="/" element={<Home pokelist={pokelist} />} />/
        <Route path="/pokemon/:id" element={<PokeProfile />} />/
      </Routes>
    </BrowserRouter>
  );
}

export default App;
