import { Col, Container, Row } from "react-bootstrap";
import { Link } from "react-router-dom";

export const Home = (props) => {
  let pokelist = props.pokelist;
  return (
    <div className="Home">
      <Container>
        <Row>
          {pokelist.map((pokemon) => (
            <Col lg={4}>
              <Link to={"/pokemon/" + pokemon.name}>{pokemon.name}</Link>
            </Col>
          ))}
        </Row>
      </Container>
    </div>
  );
};
